import Breadbar from "~/components/Breadbar.vue";
import Wordlist from "~/components/Wordlist.vue";
import Rule from "~/components/Rule.vue";
export default {
  name: "homepage",
  components: [ Breadbar, Wordlist, Rule ],
  data() {
    return {
      statusselect: 1,
    }
  },
  methods: {
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
